	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Scripting News: \"Manila Express? is","date":"17 Feb 2000","slug":"/2000-02-17-scripting-news-manila-express-is/","tags":"Y2000"},"html":"<p><a href=\"http://www.scripting.com/\">Scripting News</a>: \"Manila Express? is ready!\" <i>I'm posting with it now! It's not everyday that I'll power down Linux to try out something under Windows...</i></p>\n<p>Mama got linked by <a href=\"http://carpediem.editthispage.com/\">carpe diem</a></p>"},"next":{"fields":{"title":"Drastic Measures","date":"16 Feb 2000","slug":"/2000-02-16-drastic-measures/"}},"previous":{"fields":{"title":"Friday","date":"18 Feb 2000","slug":"/2000-02-18-friday/"}}},{"node":{"fields":{"title":"Drastic Measures","date":"16 Feb 2000","slug":"/2000-02-16-drastic-measures/","tags":"Y2000"},"html":"<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/02/crh0216.jpg\" height=\"209\" width=\"350\" border=\"0\" alt=\"crh0216.jpg: \" /></p>\n<p>Time to break out the baby pictures... I need hits for the <a href=\"http://www.editthispage.com/Top100\">Top 100</a>!</p>\n<p>About to replace me in the Top 10, and much more deserving, is the <a href=\"http://popo.editthispage.com/\">discussion section</a> of <a href=\"http://www.popo.at/\">popo.at</a>, an Austrian site with politcial reaction to their new government. popo stands for \"politcal potential\". <i>Move your popo and your ass will follow!</i> </p>\n<p><img src=\"http://www.papascott.de/wordpress/wp-content/uploads/2000/02/twingo.jpg\" height=\"102\" width=\"175\" border=\"0\" alt=\"twingo.jpg: \" /><br />\nMy Twingo is in the shop today for planned service. So I'm driving a big Megane station wagon today. <i>Before we became parents, we used to call station wagons 'Pampers Bombers'.</i></p>\n<p>BBC: <a href=\"http://news.bbc.co.uk/hi/english/world/europe/newsid_645000/645038.stm\">German scandal topples party leader</a> \"The CDU's crisis must not be allowed to become a crisis for democracy. This goal overrides all others.\"</p>\n<p>Furnace update: It turns out it had overheated yesterday, and the plumber only needed to push the reset button (marked 'For Technician Use Only'). He didn't find anything else wrong. <i>Is this something like Ctrl-Alt-Delete?</i></p>\n<p><a href=\"http://brdbrain.editthispage.com/2000/02/15\">Kate</a>, it's not always an advantage to be able to read your work E-mail at home!</p>"},"next":{"fields":{"title":"","date":"15 Feb 2000","slug":"/2000-02-15-jokes-on-yoe/"}},"previous":{"fields":{"title":"Scripting News: \"Manila Express? is","date":"17 Feb 2000","slug":"/2000-02-17-scripting-news-manila-express-is/"}}},{"node":{"fields":{"title":"","date":"15 Feb 2000","slug":"/2000-02-15-jokes-on-yoe/","tags":"Y2000"},"html":"<p>From <a href=\"http://www.internetworld.de/index_1711.html\">internetworld.de</a>: Haider's party web site faked! http://www.fpo.at (<a href=\"http://www.fpo.at/englisch/welcome.html\">English start page</a>) The fake partner site http://www.ovp.at is 'closed due to takeover' and redirected to fpo.at! (The rl sites of the FPÖ and ÖVP are http://www.fpoe.at and http://www.oevp.at, but non-German speakers wouldn't think of adding the extra 'e'.)</p>\n<p>Ack! We just noticed at 9pm that out combination furnace/water heater has gone out! Mama had noticed a funny smell this afternoon and had turned the thermostat to off. Now it won't come back on. <i>Time to call an expert... niether of us can even read the manual!</i></p>\n<p>Don't worry, Christopher shares his space heater with us.</p>\n<p>View From a Iowa Homestead: <a href=\"http://vfih.editthispage.com/2000/02/13\">Catnaps</a> <i>With pictures!</i></p>"},"next":{"fields":{"title":"Edit His Page","date":"14 Feb 2000","slug":"/2000-02-14-edit-his-page/"}},"previous":{"fields":{"title":"Drastic Measures","date":"16 Feb 2000","slug":"/2000-02-16-drastic-measures/"}}}],"pathPrefix":"","first":false,"last":false,"index":1079,"pageCount":1101}}

